/**
 * Get all Japanese holidays as a Set for fast lookup
 */
export const getJPHolidaySet = (): Set<string> => {
  const holidays = new Set<string>();
  Object.values(JP_HOLIDAYS).forEach((yearHolidays) => {
    yearHolidays.forEach((holiday) => holidays.add(holiday));
  });
  return holidays;
};

/**
 * Cached Japanese holidays set for performance
 */
export const JP_HOLIDAYS_SET = getJPHolidaySet();
